html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font-family:"Roboto",sans-serif;font-style:normal;font-weight:normal;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}html{font-size:62.5%}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}*{box-sizing:border-box}body{background-color:#1b1d22;width:100%}body #root{width:100%;height:100vh;display:flex;flex-direction:row;justify-content:space-around}body #root .main{width:72%;margin-top:40px;height:-webkit-max-content;height:max-content;display:flex;flex-direction:column}@media(max-width: 700px){body #root{flex-direction:column;justify-content:flex-start}body #root .main{width:100%;margin-top:0}}.navBar{width:18.75%;margin-top:40px;display:flex;flex-direction:column}.navBar .logo{width:100%}.navBar .logo img{max-width:100%;height:42px}.navBar h4{margin-top:40px;font-family:"Lato",sans-serif;font-size:2rem;text-transform:uppercase;color:#979797;line-height:190%}.navBar nav{overflow-y:auto;display:flex;flex-direction:column}.navBar nav a{margin:7px 0px;text-decoration:none;font-size:1.6rem;line-height:150%;color:#fff;cursor:pointer}.navBar nav a.selected{background:linear-gradient(90deg, rgba(45, 48, 56, 0) 22.92%, #2D3038 100%);border-radius:10px}.navBar .burger{display:none}@media(max-width: 700px){.navBar{width:100%;margin-top:0;height:auto;flex-direction:row;justify-content:space-between;padding:20px}.navBar .logo{width:-webkit-max-content;width:max-content;height:3vh}.navBar .logo img{height:3vh}.navBar h4{display:none}.navBar .navigation{position:absolute;background-color:#1b1d22;z-index:1;top:140px;left:0;width:0;height:0;padding:0 5%}.navBar .navigation.opened{width:100%;height:calc(100vh - 140px)}.navBar .burger{width:30px;height:30px;display:flex;flex-direction:column;justify-content:space-between}.navBar .burger div{width:100%;height:25%;background-color:#fff;transition:.4s}.navBar .burger.opened .bar1{-webkit-transform:translateY(11.1px) rotate(45deg);transform:translateY(11.1px) rotate(45deg)}.navBar .burger.opened .bar2{opacity:0}.navBar .burger.opened .bar3{-webkit-transform:translateY(-11.1px) rotate(-45deg);transform:translateY(-11.1px) rotate(-45deg)}}.search{width:100%}.search input{width:30%;height:50px;border-radius:10px;background-color:#fff;background-repeat:no-repeat;background-position-x:5px;background-position-y:center;background-size:20px;padding-left:30px;font-size:1.6rem;line-height:150%;color:#000}@media(max-width: 700px){.search{display:none;justify-content:center}.search input{width:80%}.search.opened{display:flex}}.company{width:100%;min-height:calc(100vh - 170px);margin-top:40px;background:linear-gradient(124.01deg, #2D3038 0%, rgba(45, 48, 56, 0) 100%);border-radius:30px;padding:29px 51px}.company h1{font-size:6.4rem;line-height:116%;color:#fff}.company h2{font-style:normal;font-weight:normal;font-size:1.6rem;line-height:150%;color:#979797}.company h3{font-style:normal;font-weight:normal;font-size:1.6rem;line-height:150%;text-transform:uppercase;color:#979797}.company input{width:40%;height:50px;padding:13px 2.2%;background:#fff;border-radius:10px;font-size:1.6rem;line-height:150%;color:#000}.company h4{font-size:3.4rem;line-height:129%;color:#979797}.company h5{font-size:5.6rem;line-height:118%;color:#fff}@media(max-width: 700px){.company{width:90%;height:calc(100vh - 140px);align-self:center;padding:7% 5%;margin-top:20px}.company h1{font-size:2.8rem;line-height:136%}.company input{width:100%;height:40px;padding:10px 19px}.company h4{width:100%;text-align:center;font-size:1.8rem;line-height:111%;margin-top:30px}.company h5{width:100%;text-align:center;font-size:3.4rem;line-height:129%}}
